import React, { useEffect, useState } from 'react';
import TextField from '@mui/material/TextField';
import Box from '@mui/material/Box';
import { Button } from '@mui/material';

const GoWasmComponent: React.FC = () => {
    const [qrCodeResult, setQRcodeResult] = useState("");
    useEffect(() => {
        createQR('Welcome to QR Code Generator!')
    }, []);

    const createQR = (value: string) => {
        setQRcodeResult((window.functions.generateQR!)(value));
    };

    const handleDownload = () => {
        const link = document.createElement('a');
        link.href = `data:image/png;base64,${qrCodeResult}`;
        link.download = 'qrcode.png';
        link.click();
    };

    return (
        <div>
            <Box sx={{ display: 'flex' }}>
                <Box sx={{ width: '50%' }}>
                    <TextField
                        fullWidth
                        variant="outlined"
                        multiline
                        rows={10}
                        placeholder="Welcome to QR Code Generator!"
                        onChange={e => createQR(e.target.value)}
                    />
                </Box>
                <Box sx={{ width: '50%' }}>
                    {qrCodeResult ? (
                        <img id="qrImage" src={`data:image/png;base64,${qrCodeResult}`} alt="QR Code" />
                    ) : null}
                </Box>
            </Box>
            <Box sx={{ display: 'flex', justifyContent: 'center', marginTop: '1rem' }}>
                <Button variant="contained" color="primary" onClick={handleDownload}>Download</Button>
            </Box>
        </div>
    );
};

export default GoWasmComponent;